import { Component, OnInit, ViewChild, ElementRef, Input, Output, EventEmitter } from '@angular/core';
import { loadModules } from 'esri-loader';

import { SharedService } from '../services/shared.service';
import { MapStateService } from '../services/map-state.service'

import { Subscription } from 'rxjs';


@Component({
  selector: 'coc-map',
  templateUrl: './map.component.html'
})
export class MapComponent implements OnInit {

  public mapView: __esri.MapView;

  private subscription: Subscription;
  private isMapView: any;
  private clicked: boolean = false;
  public businessResults: any[] = [];

  private restaurantIcon: string = "M344.1 470.3l14.2-164.8c-42.1-33.1-70.4-77-70.4-129.5C288 81.7 376.1 0 440 0c22.1 0 40 17.3 40 38.5v435c0 21.2-17.9 38.5-40 38.5h-56c-22.8 0-41.8-18.7-39.9-41.7zM320 176c0 51 32.2 85.5 71.8 114.5L376 473.1c-.3 3.7 3.4 6.9 8 6.9h56c4.3 0 8-3 8-6.5v-435c0-3.5-3.7-6.5-8-6.5-44.6 0-120 65.8-120 144zM240.7 33.8C237.4 14.3 219.5 0 194.6 0c-11.9 0-24.1 3.4-33.3 11.2C152.9 4.1 141.3 0 128 0s-24.9 4.1-33.3 11.2C85.5 3.4 73.3 0 61.4 0 36.2 0 18.6 14.5 15.3 33.8 13.5 43.2 0 118.4 0 149.9c0 50.9 26.7 91.6 71 110.7L59.6 471.4C58.4 493.4 75.9 512 98 512h60c22 0 39.6-18.5 38.4-40.6L185 260.6c44.2-19.1 71-59.8 71-110.7 0-31.5-13.5-106.7-15.3-116.1zM152.3 240l12.2 233.1c.2 3.7-2.7 6.9-6.5 6.9H98c-3.7 0-6.7-3.1-6.5-6.9L103.7 240C61.3 231.2 32 197 32 149.9c0-29.7 14.8-110.6 14.8-110.6 1.6-9.9 28.3-9.7 29.5.2V162c.9 11.5 28.2 11.7 29.5.2l7.4-122.9c1.6-9.7 27.9-9.7 29.5 0l7.4 122.9c1.3 11.4 28.6 11.2 29.5-.2V39.6c1.2-9.9 27.9-10.1 29.5-.2 0 0 14.8 80.9 14.8 110.6.1 46.8-29 81.2-71.6 90z"
  private retailIcon: string = "M602 118.6L537.1 15C531.3 5.7 521 0 510 0H106C95 0 84.7 5.7 78.9 15L14 118.6c-29.6 47.2-10 110.6 38 130.8v227.4c0 19.4 14.3 35.2 32 35.2h448c17.7 0 32-15.8 32-35.2V249.4c48-20.2 67.6-83.6 38-130.8zm-70 358.2c0 2-.8 3.1-.2 3.2l-446.6.3c-.3-.2-1.2-1.3-1.2-3.5V352h448zM84 320v-64h2.5c29.6 0 55.8-13 73.8-33.1 18 20.1 44.3 33.1 73.8 33.1 29.6 0 55.8-13 73.8-33.1 18 20.1 44.3 33.1 73.8 33.1 29.6 0 55.8-13 73.8-33.1 18.1 20.1 44.3 33.1 73.9 33.1h2.5v64zm494.2-126.5c-7.8 16.6-22.1 27.5-39.3 29.8-3.1.4-6.2.6-9.4.6-19.3 0-37-8-50-22.5L455.7 175l-23.8 26.6c-13 14.5-30.7 22.5-50 22.5s-37-8-50-22.5L308 175l-23.8 26.6c-13 14.5-30.7 22.5-50 22.5s-37-8-50-22.5L160.3 175l-23.8 26.6c-13 14.5-30.7 22.5-50 22.5-3.2 0-6.3-.2-9.4-.6-17.2-2.3-31.5-13.2-39.3-29.8-8.7-18.6-7.5-40.8 3.3-57.9L106 32h404l64.9 103.6c10.8 17.2 12 39.3 3.3 57.9z";


  @ViewChild('map') private mapViewEl: ElementRef;

  constructor(
    public sharedService: SharedService,
    private mapService: MapStateService
  ) { }

  ngOnInit() {
    this.sharedService.currentResults$.subscribe(
      businessResults => {
        this.businessResults = businessResults;
        this.initializeMap(this.businessResults);
      }
    )

    this.initializeMap(this.businessResults);  
  }

  showCard() {
    this.clicked = !this.clicked;
  }

  closeCard() {
    this.clicked = !this.clicked;
  }

  initializeMap(businessResults) {
    return loadModules([
      'esri/Map',
      'esri/views/MapView',
      'esri/Graphic',
      'esri/symbols/SimpleMarkerSymbol'
    ], { version: '4.9' })
    .then(([Map, MapView, Graphic, SimpleMarkerSymbol]) => {
      const map: __esri.Map = new Map({
        basemap: 'streets'
      });

      this.mapView = new MapView({
        container: this.mapViewEl.nativeElement,
        center: [-111.8436887, 33.287294],
        zoom: 13,
        map: map
      })

      this.mapView.when(
        () => {
          businessResults.forEach(x => {
            const pointGraphic: __esri.Graphic = new Graphic({
              geometry: {
                type: 'point',
                longitude: x.longitude,
                latitude: x.latitude
              },
              symbol: {
                type: 'simple-marker',
                path: x.parentCategory === "Restaurants" ? this.restaurantIcon : this.retailIcon,
                color: '#1A8AC0',
                size: "26px"
              }
            })
            this.mapView.graphics.add(pointGraphic);
          });
        },
        (err) => {
          console.log(err);
        }
      )

      this.mapView.on('click', (event: __esri.MapViewClickEvent) => {
        console.log(event.mapPoint.longitude, event.mapPoint.latitude)
      })
    })
  }
}
