  checkAll(ev) {
    this.restaurants.forEach(x => x.state = ev.target.checked);
  }

  isAllChecked() {
    return this.restaurants.every(_ => _.state);
  }

  checkAllRetail(ev) {
    this.businesses.forEach(x => x.state = ev.target.checked);
  }

  isAllRetailChecked() {
    return this.businesses.every(_ => _.state);
  }

  constructor(public sharedService: SharedService) { 
     
  }

  ngOnInit() {
    this.sharedService.getView$.subscribe(
      isListView => {
        this.isListView = isListView;
      }
    )

    this.sharedService.currentResults$.subscribe(
      businessesResults => {
        this.businessesResults = businessesResults;
      }
    )

    this.sharedService.getCurrentView$.subscribe(
      isMapView => {
        this.isMapView = isMapView;
      }
    )
  }

  toggleListView() {
    this.sharedService.setView(!this.isListView);
  }

  search() {
    this.sharedService.setBusinessResults(this.businessesResults);
    if(this.isMapView) { this.toggleListView(); }
  }

