import { Injectable } from '@angular/core';
import { Subject, BehaviorSubject, of } from 'rxjs'; 

@Injectable({ providedIn: "root" })
export class SharedService {

  private isActive = new BehaviorSubject<boolean>(false);
  private isListView = new BehaviorSubject<boolean>(false);
  private isMapView = new BehaviorSubject<boolean>(true);
  private businessResults = new Subject<any>();

  getState$ = this.isActive.asObservable();
  getView$ = this.isListView.asObservable();
  getCurrentView$ = this.isMapView.asObservable()
  currentResults$ = this.businessResults.asObservable();

  setState(state: boolean) {
    this.isActive.next(state);
  }

  setView(state: boolean) {
    this.isListView.next(state);
  }

  toggleView(state: boolean) {
    this.isMapView.next(state);
  }

  setBusinessResults(data: any) {
    this.businessResults.next(data);
  }

}