import { Component, OnInit } from '@angular/core';

import { SharedService } from '../services/shared.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'coc-desktop-controls',
  templateUrl: './desktop-controls.component.html'
})
export class DesktopControlsComponent implements OnInit {
  private subscription: Subscription; 
  private isActive: boolean;
  private isListView: boolean; 
  private isMapView: boolean;
  private businessResults: any[] = [];

  constructor(public sharedService: SharedService) { 
    
  }

  ngOnInit() {
    this.sharedService.getState$.subscribe(
      isActive => {
        this.isActive = isActive;
      }
    )

    this.sharedService.getView$.subscribe(
      isListView => {
        this.isListView = isListView;
      }
    )

    this.sharedService.getCurrentView$.subscribe(
      isMapView => {
        this.isMapView = isMapView;
      }
    )

    this.sharedService.currentResults$.subscribe(
      businessesResults => {
        this.businessResults = businessesResults;
      }
    )
  }

  toggleListView() {
    //If is map view (default view)
    if(this.isMapView && this.businessResults.length == 0) { 
      this.sharedService.toggleView(!this.isMapView);
    } else if(this.isMapView && this.businessResults.length > 0) {
      this.sharedService.toggleView(!this.isMapView);
      this.sharedService.setView(!this.isListView);
    }
  }

  toggleMapView() {
    if(!this.isMapView && this.businessResults.length == 0) {
      //If is not map view and no search results
      this.sharedService.toggleView(!this.isMapView);
    } else if (!this.isMapView && this.businessResults.length > 0) {
      this.sharedService.toggleView(!this.isMapView);
      this.sharedService.setView(!this.isListView);
    }
  }

}
